.footer-container {
    background-color: darkcyan;
    margin-top: 1rem;
    padding: 1rem;
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
}
.footer-content {
    text-align: center;
    padding: 9px;
}
.footer-social-list {
    display: flex;
    list-style: none;
    padding-left: 0;
}
.footer-social a {
    padding: 0 15px;
    text-decoration: none;
    color: #eee;
}
a:hover {
    color: rgb(3, 228, 228);
    text-decoration: underline;
}
.footer-social {
    display: flex;
    flex-direction: column;
    align-items: center;
}
