/*Customer-Transaction css container startss*/

.customer-container,
.transaction-container {
    display: flex;
    padding: 0;
    margin: 0;
    flex-direction: column;
    justify-content: space-around;
}

/*Customer-Transaction css container ends*/
/*Home Page Starts*/

.home-box-welcome {
    background-color: white;
    height: 30vh;
    color: darkcyan;
    font-size: 1.6rem;
    text-align: center;
}

.home-welcome-logo {
    max-width: 10%;
    float: center;
}

.home-box-links {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px;
    background-color: #8b8878;
    height: 55vh;
    border: 1px solid #888;
}

.home-box-buttons {
    display: flex;
    justify-content: center;
    text-align: center;
    height: 10vh;
    padding: 20px;
    margin-bottom: 2rem;
    width: 50%;
    font-size: 1.5rem;
}

.heading-center {
    color: rgb(126, 134, 136);
    text-align: center;
}

/*Home Page Ends*/
/*Form Starts*/

form {
    margin: 24px 0;
    padding: 25px 20px 15px 20px;
    border: 1px solid #ddd;
    border-radius: 4px;
}

input[type="text"] {
    width: 100%;
    padding: 12px;
    border: 1px solid #ddd;
}

button {
    margin-top: 10px;
    background-color: darkcyan;
    color: white;
    padding: 12px 20px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
}

button:hover {
    background-color: rgb(0, 105, 105);
    color: white;
}

/*Form Ends*/
/*Table Starts*/

.content-table {
    border-collapse: collapse;
    margin: 24px auto;
    font-size: 0.9rem;
    max-width: 500;
    border-radius: 5px 5px 0 0;
    overflow: hidden;
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.15);
}

.content-table thead tr {
    background-color: darkcyan;
    color: white;
    text-align: left;
    font-weight: bold;
}

.content-table td,
.content-table th {
    padding: 16px 24px;
}

.content-table tbody tr {
    border-bottom: 1px solid #ddd;
}

.content-table tbody tr:nth-of-type(even) {
    background-color: #f3f3f3;
}

.content-table tbody tr:last-of-type {
    border-bottom: 2px solid darkcyan;
}

.content-table tbody tr.active-row {
    font-weight: bold;
    color: cyan;
}

/* Tables End*/
/* About page Start*/

.about-container {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 80%;
    display: block;
    margin: auto;
    padding-top: 100px;
}

.about-tsf {
    display: flex;
    align-items: center;
    padding: 20px;
    text-transform: uppercase;
    text-align: center;
}
.about-side-words {
    text-align: center;
    float: left;
    width: 55%;
    padding-top: 40px;
    margin: 20px;
}
.about-title {
    padding-bottom: 20px;
}
.about-p {
    padding-bottom: 20px;
}
.about-tsf-logo {
    max-width: 21%;
    float: right;
    border: 1px solid black;
    padding: 10px;
    margin: 20px;
    border-radius: 40px;
    box-shadow: 0 0 10px #999;
}
