.navbar {
    height: 10vh;
    background-color: teal;
    display: flex;
    justify-content: center;
    align-items: center;
}

.navbar-logo {
    color: white;
    text-decoration: none;
    font-size: 2rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 5rem;
}

.fa-university {
    padding: 5px;
}

.menu-items {
    display: flex;
    list-style: none;
    text-align: center;
    width: 80vw;
    justify-content: flex-end;
    margin-right: 2rem;
}

.menu-item {
    display: flex;
    align-items: center;
}

.menu-item a {
    text-decoration: none;
    color: white;
    font-size: 1.1rem;
    margin-right: 2rem;
    padding: 6px 16px;
}

.nav-item:hover {
    background: rgb(9, 53, 53);

    transition: all 0.2s ease-out;
}
.fa-bars,
.fa-times {
    color: white;
}

.menu-icon {
    display: none;
}

@media (max-width: 960px) {
    .navbar {
        position: relative;
    }

    .menu-items {
        display: flex;
        flex-direction: column;
        width: 100%;

        position: absolute;
        top: 70px;
        left: -100%;
        opacity: 1;
        transition: all 0.5s ease;
    }

    .menu-item {
        text-align: center;
        padding: 2rem;
        width: 100%;
        display: table;
    }

    .menu-item:hover {
        background: rgb(9, 53, 53);
        border-radius: 0;
    }

    .navbar-logo {
        position: absolute;
        top: 0;
        left: 0;
        transform: translate(-2%, 50%);
        font-size: 1.8rem;
        cursor: pointer;
    }

    .menu-icon {
        display: block;
        position: absolute;
        top: 0;
        right: 0;
        transform: translate(-100%, 60%);
    }

    .menu-items.active {
        background: teal;

        left: 0;
        opacity: 1;
        transition: 0.5s ease;
        z-index: 1;
    }

    .fa-times {
        color: white;
        font-size: 2rem;
    }
    .fa-bars {
        color: white;
        font-size: 2rem;
    }
}
